/*
0-480px: Small Phones 
480 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop


1em = 16px
*/
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  letter-spacing: 0.03rem;
  line-height: 1.7;
  color: #707070;
  background-color: #fff;
  padding: 0;
  font-size: 1.6rem;
}

.heading-secondary {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 2.8rem;
  color: #83d500;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  padding: 1rem;
}
@media only screen and (max-width: 75em) {
  .heading-secondary {
    letter-spacing: 0.02rem;
  }
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

.btn {
  border: none;
  padding: 1rem 2rem;
  border-bottom-right-radius: 10px;
  font-size: 1.6rem;
}
.btn:focus {
  outline: none;
}
@media only screen and (max-width: 37.5em) {
  .btn {
    padding: 0.5rem 1rem;
  }
}
.btn__full {
  background-color: #6accff;
  color: #fff;
}
.btn__full:hover {
  background-color: #9fdaf9;
}
.btn__outline {
  background-color: transparent;
  border: solid 1px #6accff;
}
.btn__outline:hover {
  border: solid 1px #83d500;
}

.refresh {
  height: 1.2rem;
  padding-right: 0.8rem;
}

.btn__refresh {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem 0.6rem;
  color: #6accff;
  font-size: 1.3rem;
  margin-left: 3rem;
}
@media only screen and (max-width: 37.5em) {
  .btn__refresh {
    margin-left: 1rem;
  }
}

.decor {
  position: absolute;
  max-height: 100%;
  width: 20%;
  bottom: 0;
  opacity: 0.6;
}
.decor__left {
  left: 0;
}
.decor__right {
  right: 0;
}
@media only screen and (max-width: 56.25em) {
  .decor {
    display: none;
  }
}

.header {
  height: 25vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__logo {
  height: 3.5rem;
  margin-top: 1rem;
}
@media only screen and (max-width: 37.5em) {
  .header__logo {
    height: 3rem;
  }
}

.section-enterKey {
  height: 30vh;
}

.enterKey__box {
  height: 4.5rem;
  margin-bottom: 5vh;
  display: flex;
  justify-content: center;
}

.enterKey__key {
  height: 100%;
  width: 40rem;
  border: solid 1px rgba(112, 112, 112, 0.1);
  padding-left: 1.5rem;
  font-size: 1.5rem;
  color: #707070;
}
@media only screen and (max-width: 56.25em) {
  .enterKey__key {
    width: 62vw;
  }
}
.enterKey__key::placeholder {
  font-weight: 400;
  font-size: 1.5rem;
  font-family: 'Quicksand', sans-serif;
  color: rgba(112, 112, 112, 0.5);
}
.enterKey__key:focus {
  outline: none;
  border-bottom: solid 2px #9fdaf9;
}

.enterKey__submit {
  height: 100%;
  letter-spacing: 0.05rem;
}

.status {
  text-align: center;
  font-size: 90%;
}

.section-players {
  height: 45vh;
  width: 100vw;
  background: #f4fbff;
  position: relative;
}

.heading {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 2vh;
}

.players {
  margin: 0 auto;
  text-align: center;
}

.players__list {
  display: inline-block;
  margin: 1rem auto 2rem;
  font-size: 95%;
}
@media only screen and (max-width: 37.5em) {
  .players__list {
    display: block;
  }
}

.player {
  width: 65rem;
  list-style: none;
  margin-top: 1rem;
}
@media only screen and (max-width: 56.25em) {
  .player {
    width: 82vw;
  }
}
@media only screen and (max-width: 37.5em) {
  .player {
    width: 100vw;
    padding: 0 2rem;
    margin-top: 1.5rem;
  }
}
.player__key {
  float: left;
  width: 55rem;
  text-align: left;
}
@media only screen and (max-width: 56.25em) {
  .player__key {
    width: 70vw;
  }
}
@media only screen and (max-width: 37.5em) {
  .player__key {
    width: 100%;
    text-align: left;
    font-size: 82%;
  }
}
.player__balance {
  float: right;
  width: 10rem;
  text-align: right;
  color: #83d500;
}
@media only screen and (max-width: 56.25em) {
  .player__balance {
    width: 12vw;
  }
}
@media only screen and (max-width: 37.5em) {
  .player__balance {
    width: 8rem;
    text-align: right;
    padding: 0 3rem;
    background-color: rgba(159, 218, 249, 0.15);
  }
}
.player::after {
  content: '';
  display: table;
  clear: both;
}
